import i18n from "i18n/i18n";
import React, { useEffect, useState } from "react";
import { Input, Label } from "reactstrap";
import "./SyllabusInfo.css";
const SyllabusInfo = ({
    className,
    title,
    heDaoTao = [],
    namHoc = [],
    monHoc = [],
    handleSetSyllabus,
}) => {
    const [heDaoTaoState, setHeDaoTao] = useState("");
    const [namHocState, setNamHoc] = useState("");
    const [monHocState, setMonHoc] = useState("");
    const handleChangeValueHDT = (e) => {
        setHeDaoTao(e?.target?.value);
        handleSetSyllabus({
            idHeDaotao: e?.target?.value,
            idNamHoc: namHocState,
            idMonHoc: monHocState,
        });
    };
    const handleChangeValueNamHoc = (e) => {
        setNamHoc(e?.target?.value);
        handleSetSyllabus({
            idHeDaotao: heDaoTaoState,
            idNamHoc: e?.target?.value,
            idMonHoc: monHocState,
        });
    };
    const handleChangeValueMonHoc = (e) => {
        setMonHoc(e?.target?.value);
        handleSetSyllabus({
            idHeDaotao: heDaoTaoState,
            idNamHoc: namHocState,
            idMonHoc: e?.target?.value,
        });
    };
    return (
        <div className={`${className} syllabus_info`}>
            <p className="syllabus_info_title">{title}</p>
            <div className="syllabus_info_select">
                <div className="heDaoTaoField input_wrapper">
                    <Label for="he_dao_tao">
                        {i18n.t("syllabus.TRAINING_SYSYTEM")}
                    </Label>
                    <Input
                        type="select"
                        name="heDaoTao"
                        id="he_dao_tao"
                        onChange={handleChangeValueHDT}
                        defaultValue={null}
                    >
                        <option value=""></option>
                        {heDaoTao?.map((item) => (
                            <option value={item?.id} key={item?.ma}>
                                {item?.ten}
                            </option>
                        ))}
                    </Input>
                </div>
                <div className="namHocField input_wrapper">
                    <Label for="nam_hoc">{i18n.t("syllabus.SCHOLASTIC")}</Label>
                    <Input
                        type="select"
                        name="namHoc"
                        id="nam_hoc"
                        onChange={handleChangeValueNamHoc}
                        defaultValue={null}
                    >
                        <option value=""></option>
                        {namHoc?.map((item) => (
                            <option value={item?.id} key={item?.ma}>
                                {item?.ten}
                            </option>
                        ))}
                    </Input>
                </div>
                <div className="monHocField input_wrapper">
                    <Label for="mon_hoc">
                        {i18n.t("syllabus.SUBJECT_NAME")}
                    </Label>
                    <Input
                        type="select"
                        name="monHoc"
                        id="mon_hoc"
                        onChange={handleChangeValueMonHoc}
                        defaultValue={null}
                    >
                        <option value=""></option>
                        {monHoc?.map((item) => (
                            <option value={item?.id} key={item?.ma}>
                                {item?.tenTiengViet}
                            </option>
                        ))}
                    </Input>
                </div>
            </div>
        </div>
    );
};

export default SyllabusInfo;
