import { Button, Form } from "reactstrap";
import React, { useCallback, useEffect, useMemo, useState } from "react";
import SplitPane from "react-split-pane";
import SyllabusInfo from "./SyllabusInfo";
import namHocAPI from "api/common/school-year";
import heDaoTaoAPI from "api/common/heDaoTao.api";
import monHocAPI from "api/common/monHoc.api";
import syllabusAPI from "api/admin/syllabus.api";
import * as lodash from "lodash";
import "./SplitPane.css";
import "./SyllabusCompare.css";
import "../Sidebar/Sidebar.css";
import SidebarSyllabus from "../Sidebar/SideBar";
import SyllabusBasic from "views/admin/Syllabus/Syllabus_Contents/Syllabus_Basic";
import SyllabusDescription from "views/admin/Syllabus/Syllabus_Contents/Syllabus_Description";
import SyllabusGoals from "views/admin/Syllabus/Syllabus_Contents/Syllabus_Goals";
import SyllabusStandardOutput from "views/admin/Syllabus/Syllabus_Contents/Syllabus_StandardOutput";
import SyllabusPlanTheory from "views/admin/Syllabus/Syllabus_Contents/Syllabus_PlanTheory";
import SyllabusPlanPractice from "views/admin/Syllabus/Syllabus_Contents/Syllabus_PlanPractice";
import SyllabusEvaluation from "views/admin/Syllabus/Syllabus_Contents/Syllabus_Evaluation";
import SyllabusResources from "views/admin/Syllabus/Syllabus_Contents/Syllabus_Resources";
import SyllabusRules from "views/admin/Syllabus/Syllabus_Contents/Syllabus_Rules";
import i18n from "i18n/i18n";
import { toast } from "react-toastify";
import useSidebar from "stores/common/SidebarStore/SidebarStore";
const menus = [
    {
        key: "basic",
        render: (data) => <SyllabusBasic {...data} />,
    },
    {
        key: "description",
        render: (data) => <SyllabusDescription {...data} />,
    },
    {
        key: "goals",
        render: (data) => <SyllabusGoals {...data} />,
    },
    {
        key: "standard-output",
        render: (data) => <SyllabusStandardOutput {...data} />,
    },
    {
        key: "plan-theory",
        render: (data) => <SyllabusPlanTheory {...data} />,
    },
    {
        key: "plan-practices",
        render: (data) => <SyllabusPlanPractice {...data} />,
    },
    // {  key: 'evaluation', render: (data) =>  <SyllabusEvaluation {...data}/> },
    {
        key: "resources",
        render: (data) => <SyllabusResources {...data} />,
    },
    {
        key: "rules",
        render: (data) => <SyllabusRules {...data} />,
    },
];
const SyllabusCompare = ({}) => {
    const [stateSideBar, actionSideBar] = useSidebar();
    const [isCompare, setIsCompare] = useState(false);
    const [tabIndex, setTabIndex] = useState(null);
    const [namHoc, setNamHoc] = useState([]);
    const [heDaoTao, setHeDaoTao] = useState([]);
    const [monHoc, setMonHoc] = useState([]);
    const [syllabus1, setSyllabus1] = useState(null);
    const [syllabus2, setSyllabus2] = useState(null);
    const [idSyllabus1, setIdSyllabus1] = useState(null);
    const [idSyllabus2, setIdSyllabus2] = useState(null);
    useEffect(() => {
        (async () => {
            const namHocPromise = namHocAPI.getAll();
            const heDaoTaoPromise = heDaoTaoAPI.getAll();
            const monHocPromise = monHocAPI.getAll();
            const [namHocData, heDaoTaoData, monHoc] = await Promise.all([
                namHocPromise,
                heDaoTaoPromise,
                monHocPromise,
            ]);
            setNamHoc(namHocData?.contents || []);
            setHeDaoTao(heDaoTaoData?.contents || []);
            setMonHoc(monHoc?.contents || []);
        })();
    }, []);
    const handleCompareSyllabus = async () => {
        setIsCompare(false);
        if (!syllabus1) {
            toast.error(
                i18n.t("syllabus.SYLLABUS_INVALID").replace(":id", "1")
            );
            return;
        }
        if (!syllabus2) {
            toast.error(
                i18n.t("syllabus.SYLLABUS_INVALID").replace(":id", "2")
            );
            return;
        }
        if (stateSideBar?.isOpenSideBar) {
            actionSideBar.setIsOpenSideBar({ isOpenSideBar: false });
        }
        const syllabusID1 = syllabusAPI.getAll(syllabus1);
        const syllabusID2 = syllabusAPI.getAll(syllabus2);
        const [syllabusArr1, syllabusArr2] = await Promise.all([
            syllabusID1,
            syllabusID2,
        ]);
        if (syllabusArr1?.total) {
            setIdSyllabus1(syllabusArr1?.contents[0]?.id);
        } else {
            setIdSyllabus1(null);
        }
        if (syllabusArr2?.total) {
            setIdSyllabus2(syllabusArr2?.contents[0]?.id);
        } else {
            setIdSyllabus2(null);
        }
        setIsCompare(true);
    };
    const handleClickChooseTabIndex = (index) => {
        if (!syllabus1 || !syllabus2) {
            toast.error(i18n.t("syllabus.NOT_CHOSEN_SYLLABUS_INPUT"));
            return;
        }
        if (index) {
            setTabIndex(index);
        }
    };
    const handleSetSyllabus1 = (data) => {
        for (const item of Object.values(data)) {
            if (!item) {
                setSyllabus1(null);
                return;
            }
        }
        if (!lodash.isEqual(syllabus1, data)) {
            setSyllabus1({ ...data });
        }
    };
    const handleSetSyllabus2 = (data) => {
        for (const item of Object.values(data)) {
            if (!item) {
                setSyllabus2(null);
                return;
            }
        }
        if (!lodash.isEqual(syllabus2, data)) {
            setSyllabus2({ ...data });
        }
    };

    const renderContents = () => {
        let component = (data) => <SyllabusBasic {...data} />;
        if (tabIndex) {
            const result = menus.find((item) => item?.key?.includes(tabIndex));
            if (result) {
                component = result?.render;
            }
        }
        return (
            <SplitPane
                style={{ position: "relative" }}
                split="vertical"
                defaultSize="50%"
            >
                <div className="split_view_container">
                    {idSyllabus1 ? (
                        component({
                            idSyllabus: idSyllabus1,
                        })
                    ) : (
                        <p>{i18n.t("ERROR")}</p>
                    )}
                </div>
                <div className="split_view_container">
                    {idSyllabus2 ? (
                        component({ idSyllabus: idSyllabus2 })
                    ) : (
                        <p>{i18n.t("ERROR")}</p>
                    )}
                </div>
            </SplitPane>
        );
    };
    return (
        <div className="syllabus_compare_container">
            <div className="syllabus_tools_container">
                <div className="syllabus_tools">
                    <SyllabusInfo
                        title="Syllabus#1"
                        className="syllabus_1"
                        namHoc={namHoc}
                        heDaoTao={heDaoTao}
                        monHoc={monHoc}
                        handleSetSyllabus={handleSetSyllabus1}
                    />
                    <SyllabusInfo
                        title="Syllabus#2"
                        className="syllabus_2"
                        namHoc={namHoc}
                        heDaoTao={heDaoTao}
                        monHoc={monHoc}
                        handleSetSyllabus={handleSetSyllabus2}
                    />
                </div>
                <div className="syllabus_tools_btn">
                    <Button color="primary" onClick={handleCompareSyllabus}>
                        {i18n.t("syllabus.COMPARE")}
                    </Button>
                </div>
            </div>
            <div className="syllabus_compare_contents">
                <div className="syllabus__sidebar syllabus_compare_sidebar">
                    <SidebarSyllabus
                        handleClickChooseTabIndex={handleClickChooseTabIndex}
                        tabIndex={tabIndex}
                    />
                </div>
                <div className="syllabus_compare_splitpane">
                    {isCompare && renderContents()}
                </div>
            </div>
        </div>
    );
};

export default SyllabusCompare;
